
@tailwind base;
@tailwind components;
@tailwind utilities;

body{

    background-color: #F2F2F2;
    display: flex;
    justify-content: center;
}


   



.checkBox{
     
  display: block;
  border-radius: 50%;
  background: white;
  border: 1px solid #E3E4F1;
  width: 20px;
  height: 20px;
  display: flex;
  cursor: pointer;
}

.checkBox-checked {
    background: no-repeat center url("./images/path.png"), linear-gradient(135deg, #55DDFF 0%, #C058F3 100%);
    color: #25273D;
    cursor: pointer;
};

